  font-family: "Rubik", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
}

* {
  color: #ffffffde;
}

:root {
  --font-color: #4e4e4e;
  --box-color: #ffffff2a;
  --input-color: #ffffffbe;
  --border-radius: 20px;
  --box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
  --midday: linear-gradient(#6bcfff, #4bc5ff, #33beff, #27b9ff, #00adff);
  --midnight: linear-gradient(#282442, #1d1936, #131122, #0a0913, #000000);
  --sunset: linear-gradient(#01084f, #391954, #631e50, #a73c5a, #ff7954);
  --sunrise: linear-gradient(#b5d6e0, #c7e1e5, #ffef7a, #f7c16a, #f46a55);
  --rain: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  --clouds: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  --snow: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

body {
  margin: 0;
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Rubik", sans-serif;
  background-image: var(--sunset);
}

#weatherGif {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: contain;
}

.container {
  height: 70vh;
  width: max(55vw, 800px);
  background-color: var(--box-color);
  display: flex;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

.left {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 4fr 3fr 3fr 4fr;
  margin: 20px;
  margin-right: 10px;
  background-color: var(--box-color);
  border-radius: var(--border-radius);
  text-align: center;
  align-items: stretch;
  box-shadow: var(--box-shadow);
}

#search {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 10px 0 15px;
}

#locationInput {
  height: 25px;
  border-radius: 15px;
  width: 200px;
  border: none;
  padding-left: 10px;
  margin-right: -25px;
  color: var(--font-color);
  background-color: var(--input-color);
}

#searchButton {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-position: center;
  background-size: contain;
  height: 20px;
  width: 20px;
  display: inline-block;
  position: relative;
  top: 5px;
  right: 5px;
}

#search > select {
  height: 25px;
  border-radius: 15px;
  margin-right: 5px;
  border: none;
  padding-left: 10px;
  width: 110px;
  color: var(--font-color);
  background-color: var(--input-color);
}

#search > select > option {
  color: var(--font-color);
}

#leftUpper {
  grid-row: 2 / 3;
  grid-column: 1 / 3;
  display: flex;
  flex-flow: column;
  justify-content: center;
  border-bottom: solid 1px #929292;
  margin: 0 20px 0 20px;
}

#leftUpper > * {
  margin: 0;
}

#location {
  font-size: 2.5em;
  font-weight: 400;
}

#time,
#date {
  font-weight: 300;
}

#currentTempC,
#currentTempF {
  grid-row: 3 / 4;
  grid-column: 1 / 2;
  align-self: flex-end;
  font-size: 2.5em;
  margin-left: 20px;
}

.weatherIcon {
  grid-row: 3 / 5;
  grid-column: 2 / 3;
  width: 250px;
  height: 250px;
  justify-self: center;
  align-self: center;
}

#condition {
  grid-row: 4 / 5;
  grid-column: 1 / 2;
  font-size: 1.2em;
  font-weight: 300;
  margin-left: 20px;
}

.humidIcon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  background-position: center;
  background-size: contain;
  height: 20px;
  width: 20px;
  margin-right: 5px;
}

#bottom > div:first-child {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
}

.windIcon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
  background-position: center;
  background-size: contain;
  height: 20px;
  width: 20px;
  margin-right: 5px;
}

#bottom > div:last-child {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
}

#bottom {
  grid-row: 5 / 6;
  grid-column: 1 / 3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-top: solid 1px #929292;
  margin: 0 20px 0 20px;
  align-items: center;
}

.right {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  margin: 20px;
  margin-left: 10px;
  gap: 20px;
  font-size: 1.2em;
}

.today,
.tomorrow,
.dayAfter {
  box-shadow: var(--box-shadow);
  padding: 10px;
}

.today > div,
.tomorrow > div,
.dayAfter > div {
  font-weight: 300;
}

.today > div:first-child,
.tomorrow > div:first-child,
.dayAfter > div:first-child {
  font-weight: 400;
}

.today > div:nth-child(2),
.today > div:nth-child(3),
.tomorrow > div:nth-child(2),
.tomorrow > div:nth-child(3),
.dayAfter > div:nth-child(2),
.dayAfter > div:nth-child(3) {
  font-weight: 400;
}

.right > div {
  display: grid;
  grid-template-rows: 1fr 3fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  background-color: var(--box-color);
  text-align: center;
  align-items: center;
  border-radius: var(--border-radius);
}
.right > div > div:first-child {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
}
.right > div > #tempC,
.right > div > #tempF {
  grid-row: 1 / 2;
  grid-column: 3 / 4;
}
.six {
  grid-row: 2 / 3;
  grid-column: 1 / 2;
}
.twelve {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
}
.eighteen {
  grid-row: 2 / 3;
  grid-column: 3 / 4;
}
.six,
.twelve,
.eighteen {
  justify-self: center;
  align-self: center;
  height: 70px;
}

.hide {
  display: none;
}
